import { createWebHistory, createRouter } from "vue-router";
import ListName from './components/ListName.vue';
import HomeHome from './components/HomeHome.vue';
import DetailDetail from './components/DetailDetail.vue';
import Comment from './components/Comment.vue';
import Author from './components/Author.vue';


const routes = [
  {
    path: "/list",
    component: ListName,
  },

  {
    path: "/detail/:id",
    component: DetailDetail,
    children: [
      {
        path: "/author",
        component: Author.vue,

      },
      
      {
        path: "/comment",
        component: Comment.vue,

      }

    ],
  },

  {
    path: "/",
    component: HomeHome,
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 